import {
  ConsumedWorkersPercentage,
  ExecutionBudgetUnitId,
  ProgressReportData,
  ProjectReportId,
  ReportText,
  Title,
  TotalPerformancePercentage,
  WorkerId,
} from '../value';

import {
  BaseProjectReport,
  BaseUpdateProjectReport,
  IBaseProjectReport,
  IUpdateBaseProjectReport,
} from './BaseProjectReport';

export interface IProgressCheckReport extends IBaseProjectReport {
  title: Title;
  reportText: ReportText;
  executionBudgetUnitId: ExecutionBudgetUnitId;
  progressReportData: ProgressReportData;
  totalPerformancePercentage: TotalPerformancePercentage;
  consumedWorkersPercentage: ConsumedWorkersPercentage;
  workerIds: WorkerId[];
}

export interface IUpdateProgressCheckReport extends IUpdateBaseProjectReport {
  title?: Title;
  reportText?: ReportText;
  executionBudgetUnitId?: ExecutionBudgetUnitId;
  progressReportData?: ProgressReportData;
  totalPerformancePercentage?: TotalPerformancePercentage;
  consumedWorkersPercentage?: ConsumedWorkersPercentage;
  workerIds?: WorkerId[];
}

export class ProgressCheckReport extends BaseProjectReport {
  readonly title: Title;
  readonly reportText: ReportText;
  readonly executionBudgetUnitId: ExecutionBudgetUnitId;
  readonly progressReportData: ProgressReportData;
  readonly totalPerformancePercentage: TotalPerformancePercentage;
  readonly consumedWorkersPercentage: ConsumedWorkersPercentage;
  readonly workerIds: WorkerId[];

  constructor(args: IProgressCheckReport) {
    super(args);
    this.title = args.title;
    this.reportText = args.reportText;
    this.executionBudgetUnitId = args.executionBudgetUnitId;
    this.progressReportData = args.progressReportData;
    this.totalPerformancePercentage = args.totalPerformancePercentage;
    this.consumedWorkersPercentage = args.consumedWorkersPercentage;
    this.workerIds = args.workerIds;
  }

  update(args: IUpdateProgressCheckReport): UpdateProgressCheckReport {
    return new UpdateProgressCheckReport({ id: this.id, ...args });
  }
}

export class UpdateProgressCheckReport extends BaseUpdateProjectReport {
  readonly title?: Title;
  readonly reportText?: ReportText;
  readonly executionBudgetUnitId?: ExecutionBudgetUnitId;
  readonly progressReportData?: ProgressReportData;
  readonly totalPerformancePercentage?: TotalPerformancePercentage;
  readonly consumedWorkersPercentage?: ConsumedWorkersPercentage;
  readonly workerIds?: WorkerId[];

  constructor(args: IUpdateProgressCheckReport & { id: ProjectReportId }) {
    super(args);
    this.title = args.title;
    this.reportText = args.reportText;
    this.executionBudgetUnitId = args.executionBudgetUnitId;
    this.progressReportData = args.progressReportData;
    this.totalPerformancePercentage = args.totalPerformancePercentage;
    this.consumedWorkersPercentage = args.consumedWorkersPercentage;
    this.workerIds = args.workerIds;
  }
}
